@import 'assets/scss/utils/mixins';

button {
	width: 34px;
	height: 34px;
}

div.col.position-relative {
	padding: 0 !important;
}

.icon-size {
	width: 16px;
	height: 16px;
}

findqo-button.icon {
	display: block;
	width: 20px !important;
	height: 20px !important;
}

findqo-button.phone {
	height: 40px;
}

.phone-btn {
	height: 40px;
	width: 150px;
}

.h-40px {
	height: 40px;
}

.ad-img-btn {
	width: 35px;
	height: 35px;
	background: rgba(26, 31, 46, 0.65);
}

.ad-detail-padding {
	padding: 10px 0px 0px 0px;
}

.business-information {
	width: 55px;
	height: 40px;
	border-radius: 4px;
}

.favorite-icon {
	width: 23px;
	height: 23px;
}

.badge-icons {
	width: 16px;
	height: 16px;
}

.mat-mdc-menu-item {
	width: 177px !important;
	height: 40px !important;
}

.mat-mdc-menu-item .mat-icon {
	color: var(--general-4) !important;
	height: 20px !important;
	width: 20px !important;
	margin-right: 8px !important;
}

.mat-mdc-menu-item .mdc-list-item__primary-text {
	color: var(--general-4) !important;
}

@include media-breakpoint-up(sm) {
	.desktop-advertisement {
		position: relative;
	}

	.ad-detail-padding {
		padding: 24px 16px 16px 16px;
	}

	findqo-button.phone {
		height: 40px;
		width: 150px;
	}

	button {
		width: 38px;
		height: 38px;
	}

	.image-container {
		width: 325px;
	}

	.ad-image {
		width: 325px;
		height: 200px;
		border-start-end-radius: 0 !important;
		border-end-end-radius: 0 !important;
	}

	.remove-border-start-radius {
		border-start-start-radius: 0 !important;
	}

	.on-desktop {
		display: none;
	}

	.col-height {
		height: 100%;
	}
}

@include media-breakpoint-down(md) {
	.image-width {
		width: 100%;
	}
}

@include media-breakpoint-down(lg) {
	.ad-image {
		border-end-start-radius: 0;
	}
}

@include media-breakpoint-up(lg) {
	.desktop-advertisement {
		&.border-lg {
			border: 1px solid var(--general-2);
			border-radius: 4px;
		}
	}

	.row-max-height {
		min-height: 200px;
		max-height: 270px;
		border-top: 1px solid var(--general-2);
	}

	.image-container {
		height: 100%;
	}

	.hide-border-top {
		border-top: 0px !important;
	}
}

@include media-breakpoint-down(sm) {
	.ad-image {
		border-end-start-radius: 4px;
	}

	.ms-19px {
		margin-inline-start: 20px;
	}

	.on-mobile {
		display: none;
	}
}
